export default {
	data() {
		return {
			alertBoxIsShow: false,
			allPic: 0, //全部价格
			shopId: [], //所有商品id
			delId: 0, //删除ID
			shopCount: 0, //全部商品数量
			isAllChecked: false,
			shopList: [],
			goodsList: {
				saveHidden: true,
				totalPrice: 0,
				allSelect: true,
				noSelect: false,
				list: [],
			},
		}
	},
	onShow() {

		var shopList = [];
		// 获取购物车数据
		var shopCarInfoMem = wx.getStorageSync('addShowList');
		this.goodsList.list = shopCarInfoMem;
		shopList = shopCarInfoMem;
		console.log(shopList);
		this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), shopList);

	},
	methods: {
		// 去订单页
		goConfirmation() {
			
			var that = this;
			if (this.goodsList.noSelect) {
				wx.showToast({
					title: '请选择商品',
					image: '/static/img/login_close.png',
					duration: 2000
				})
				return;
			}
			var shopList = [];
			var shopCarInfoMem = wx.getStorageSync('addShowList');

			shopList = shopCarInfoMem.filter(entity => {
				return entity.active;
			});

			if (shopList.length == 0) {
				wx.showToast({
					title: '请选择商品',
					image: '/static/img/login_close.png',
					duration: 2000
				})
				return;
			}
			this.navigateToPayOrder();

		},
		delItem: function(index) {
			//var index = e.currentTarget.dataset.index;
			var list = this.goodsList.list;
			list.splice(index, 1);
			this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
		},
		selectTap: function(itemid, index) {
			//var index = e.currentTarget.dataset.index;
			var list = this.goodsList.list;
			if (index !== "" && index != null) {
				list[parseInt(index)].active = !list[parseInt(index)].active;
				this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
			}
		},
		totalPrice: function() {
			var list = this.goodsList.list;
			var total = 0;
			for (var i = 0; i < list.length; i++) {
				var curItem = list[i];
				if (curItem.active) {
					total += parseFloat(curItem.commodity_price) * curItem.commodity_num;
				}
			}
			total = parseFloat(total.toFixed(2)); //js浮点计算bug，取两位小数精度
			return total;
		},
		allSelect: function() {
			var list = this.goodsList.list;
			var allSelect = false;
			for (var i = 0; i < list.length; i++) {
				var curItem = list[i];
				if (curItem.active) {
					allSelect = true;
				} else {
					allSelect = false;
					break;
				}
			}
			return allSelect;
		},
		noSelect: function() {
			var list = this.goodsList.list;
			var noSelect = 0;
			for (var i = 0; i < list.length; i++) {
				var curItem = list[i];
				if (!curItem.active) {
					noSelect++;
				}
			}
			if (noSelect == list.length) {
				return true;
			} else {
				return false;
			}
		},
		setGoodsList: function(saveHidden, total, allSelect, noSelect, list) {
			this.goodsList.saveHidden = saveHidden;
			this.goodsList.totalPrice = total;
			this.goodsList.allSelect = allSelect;
			this.goodsList.noSelect = noSelect;
			this.goodsList.list = list;
			this.shopList = list;
			console.log(list);
			var shopCarInfo = {};
			var tempNumber = 0;
			shopCarInfo.shopList = list;
			for (var i = 0; i < list.length; i++) {
				tempNumber = tempNumber + list[i].number
			}
			shopCarInfo.shopNum = tempNumber;
			wx.setStorage({
				key: "addShowList",
				data: list
			})
		},
		bindAllSelect: function() {
			var currentAllSelect = this.goodsList.allSelect;
			var list = this.goodsList.list;
			if (currentAllSelect) {
				for (var i = 0; i < list.length; i++) {
					var curItem = list[i];
					curItem.active = false;
				}
			} else {
				for (var i = 0; i < list.length; i++) {
					var curItem = list[i];
					curItem.active = true;
				}
			}

			this.setGoodsList(this.getSaveHide(), this.totalPrice(), !currentAllSelect, this.noSelect(), list);
		},
		jiaBtnTap: function(itemId, jia, index) {
			// var index = e.currentTarget.dataset.index;
			var list = this.goodsList.list;
			if (index !== "" && index != null) {
				// if (list[parseInt(index)].number < 10) {
				list[parseInt(index)].commodity_num++;
				this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
				//}
			}
		},
		jianBtnTap: function(itemId, jian, index) {
			//var index = e.currentTarget.dataset.index;
			var list = this.goodsList.list;
			if (index !== "" && index != null) {
				if (list[parseInt(index)].commodity_num > 1) {
					list[parseInt(index)].commodity_num--;
					this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
				} else if (list[parseInt(index)].num == 1) {
					this.delId = index;
					this.alertBoxIsShow = true
				}
			}
		},
		editTap: function() {
			var list = this.goodsList.list;
			for (var i = 0; i < list.length; i++) {
				var curItem = list[i];
				curItem.active = false;
			}
			this.setGoodsList(!this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
		},
		saveTap: function() {
			var list = this.goodsList.list;
			for (var i = 0; i < list.length; i++) {
				var curItem = list[i];
				curItem.active = true;
			}
			this.setGoodsList(!this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
		},
		getSaveHide: function() {
			var saveHidden = this.goodsList.saveHidden;
			return saveHidden;
		},
		deleteSelected: function() {
			var list = this.goodsList.list;
			/*
			 for(let i = 0 ; i < list.length ; i++){
			       let curItem = list[i];
			       if(curItem.active){
			         list.splice(i,1);
			       }
			 }
			 */
			// above codes that remove elements in a for statement may change the length of list dynamically
			list = list.filter(function(curGoods) {
				return !curGoods.active;
			});
			this.setGoodsList(this.getSaveHide(), this.totalPrice(), this.allSelect(), this.noSelect(), list);
		},
		// 浮层关闭
		alertBoxClose() {
			this.alertBoxIsShow = false
		},
		// 删除
		alertBoxDelete(e) {
			this.delItem(this.delId);
			this.alertBoxIsShow = false
		},
		toIndexPage: function() {
			wx.switchTab({
				url: "/pages/index/index"
			});
		},
		navigateToPayOrder: function() {
			wx.hideLoading();
			wx.navigateTo({
				url: "/pages/shopConfirmation/shopConfirmation"
			})
		}
	}
}
